import React, { useEffect, useMemo, useState } from 'react'
import { Grid, Typography } from "@mui/material"
import { AI_delete_event_user, AI_event_user } from 'API/eventUserAPI'
import HeaderWrapper from 'components/Wrapper/HeaderWrapper'
import { useAIEventHandler, useInstance } from 'easy-redux-http-call'
import { formatDate } from 'utils'
import { makeStyles } from '@mui/styles'
import { IconButton } from "@mui/material"
import LaunchIcon from '@mui/icons-material/Launch';
import DeleteIcon from '@mui/icons-material/Delete';
import { useNavigate } from 'react-router'
import { GroupChip } from 'containers/Setting/UserGroups'
import { useGlobalSearch } from 'utils/hooks'
import NoSearchResultFound from 'components/Common/Dialog/general/NoSearchResultFound'
import { LoadingAnimation } from 'containers/SuspenseFallback/SuspenseFallback'
import { ConfirmationDialog } from 'components/Common/Dialog/ConfirmationDialog'

const useStyles = makeStyles((theme) => ({
  usersContainer: {
    display: "grid",
    gridTemplateColumns: "1",
    gridGap: 8,
  },
  dmUserCard: {
    background: theme.palette.primary.main + "24",
    flex: 1,
    padding: 10,
    borderRadius: theme.shape.borderRadius,
    position: "relative"
  },
  dmViewActionContainer: {
    position: 'absolute',
    top: 6,
    right: 6
  },
  dmUserGroupsContainer: {
    width: "fit-content",
    display: "inline-block",
    "&>div": {
      marginRight: 4,
    }
  }
}))

export default function Users() {
  const classes = useStyles()
  const navigator = useNavigate()

  const [showDeleteEventUserConfirmation, setShowDeleteEventUserConfirmation] = useState(false)

  const eventUserData = useInstance(AI_event_user)
  const deleteEventUserDetail = useInstance(AI_delete_event_user)

  const { searchValue } = useGlobalSearch({ placeholder: 'Search user', key: 'event-user' })

  useEffect(() => {
    AI_event_user.call()
  }, [])

  const viewDetailProfile = (id) => {
    navigator(`/person/view/${id}`)
  }

  const eventUserList = useMemo(() => {
    if (!searchValue) return eventUserData?.data?.data

    const search = searchValue?.toLowerCase()

    return (eventUserData?.data?.data?.map(event => {
      if (event.name.toLowerCase().includes(search)) return event

      if (event?.group?.find((g) => g?.name?.toLowerCase()?.includes(search))) return event

      return null
    }).filter(i => i)) || []
  }, [eventUserData?.data?.data, searchValue])

  useAIEventHandler(AI_delete_event_user, {
    onSuccess: () => setShowDeleteEventUserConfirmation(false),
  })

  const handleDeleteEventUser = () => {
    let user = showDeleteEventUserConfirmation
    AI_delete_event_user.call({
      urlParams: {
        id: user._id
      }
    })
  }

  return (
    <HeaderWrapper backAction title="Users">
      <Grid className={classes.usersContainer}>
        {
          eventUserData?.loading ?
            <LoadingAnimation />
            :
            eventUserData?.data?.data?.length ?
              eventUserList?.length ?
                eventUserList?.map(i =>
                  <Grid className={classes.dmUserCard}>
                    <Typography variant='h6'>
                      <b>Name: </b>
                      {i.name}
                    </Typography>
                    <Typography>
                      <b>Created At: </b>
                      {formatDate(i.createdAt)}
                    </Typography>
                    <Typography>
                      <b>Total events: </b>
                      {i?.events?.length}
                    </Typography>
                    <Typography>
                      <b>Groups: </b>
                      <Grid className={classes.dmUserGroupsContainer}>
                        {
                          i?.group?.length ?
                            i?.group?.map((group) =>
                              <GroupChip
                                group={group}
                                key={group._id}
                              />
                            ) : "-"
                        }
                      </Grid>
                    </Typography>
                    <div className={classes.dmViewActionContainer}>
                      <IconButton size="small" onClick={() => setShowDeleteEventUserConfirmation(i)} disabled={deleteEventUserDetail.loading}>
                        <DeleteIcon />
                      </IconButton>
                      <IconButton size="small" onClick={() => viewDetailProfile(i._id)}>
                        <LaunchIcon />
                      </IconButton>
                    </div>
                  </Grid>
                ) : <NoSearchResultFound />
              :
              <Grid display='flex' justifyContent='center' minHeight="200px" flexDirection="column" alignItems='center'>
                <Typography>No user found</Typography>
                <Typography variant='caption'>Create event user from home page.</Typography>
              </Grid>
        }
      </Grid>

      <ConfirmationDialog
        open={Boolean(showDeleteEventUserConfirmation)}
        handleClose={() => setShowDeleteEventUserConfirmation(false)}
        title="Delete Event User"
        message="Are you sure you want to delete this event user?"
        handleConfirmation={handleDeleteEventUser}
        buttonProps={{
          loading: deleteEventUserDetail.loading,
        }}
      />
      
    </HeaderWrapper>
  )
}
