import { Dialog, DialogContent, DialogActions, DialogTitle, Button, Typography } from "@mui/material"

/**
 * ConfirmationDialog component renders a dialog with a title, message, and action buttons.
 *
 * @param {Object} props - The properties object.
 * @param {boolean} props.open - Controls whether the dialog is open or closed.
 * @param {string} [props.maxWidth="xs"] - The maximum width of the dialog.
 * @param {string} [props.title="title"] - The title text displayed in the dialog.
 * @param {string} [props.message="message"] - The message text displayed in the dialog content.
 * @param {Function} [props.handleClose=() => {}] - The function to call when the cancel button is clicked.
 * @param {Function} [props.handleConfirmation=() => {}] - The function to call when the confirm button is clicked.
 * @param {Object} [props.buttonProps={}] - Additional properties for the buttons.
 * @param {Object} [props.buttonProps.cancelButton] - Properties for the cancel button.
 * @param {string} [props.buttonProps.cancelButton.text="Cancel"] - The text displayed on the cancel button.
 * @param {Object} [props.buttonProps.confirmButton] - Properties for the confirm button.
 * @param {string} [props.buttonProps.confirmButton.text="Yes"] - The text displayed on the confirm button.
 */
export const ConfirmationDialog = (props) => {
  const { open, maxWidth = "xs", title = "title", message = "message", handleClose = () => { },
    handleConfirmation = () => { },
    buttonProps = {}
  } = props

  return (
    <Dialog open={open} onClose={handleClose} maxWidth={maxWidth}>
      <DialogTitle>
        {title}
      </DialogTitle>
      <DialogContent>
        <Typography>
          {message}
        </Typography>
      </DialogContent>
      <DialogActions>
        <Button variant="text" color="inherit" onClick={handleClose} >{buttonProps?.cancelButton?.text || "Cancel"}</Button>
        <Button color="error" onClick={handleConfirmation} {...buttonProps?.confirmButton} disabled={buttonProps?.confirmButton?.disabled || buttonProps?.confirmButton?.loading} >{buttonProps?.confirmButton?.text || "Yes"}</Button>
      </DialogActions>
    </Dialog>
  )
}